html {
  font-family: sans-serif;
  font-size: 16px;
  background: #d1d8e0;
  color: #1a1b1c;
}

body {
  margin: 0;
}

.stage {
  min-height: 100vh;
  min-width: 100vw;
  position: relative;
  left: 0;
  top: 0;
  z-index: 1;
}

.canvas {
  background: transparent;
  height: 100vh;
  width: 100vw;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 2;
}

.interface {
  top: 0;
  left: 0;
  position: fixed;
  height: 100vh;
  width: 100vw;
  pointer-events: none;
  z-index: 4;
  padding: 30px;
  > * {
    pointer-events: auto;
  }
}

.btn {
  position: relative;
}

.options {
  position: absolute;
  display: none;
  z-index: 4;
  border: 2px dotted #1a1b1c;

  &--active {
    display: block;
  }
}